const md5 = require("../../utils/md5.js");
const util = require("../../utils/util.js");
const app = getApp().globalData;
import auth from "../../component/easyModal/auth/auth";
import navBar from "../../component/nav-bar/nav-bar";

export default {
  data() {
    return {
      code: '获取验证码',
      phoneNumber: '',
      codeNumber: '',
      enable: true,
      thirdEnable: true,
      canIUse: wx.canIUse('button.open-type.getUserInfo'),
      wxCode: '',
      oauth_cfg: {
        oauthTipName: '为了更好的体验\n需要您的授权\n(• ̀ω•́ )✧',
        oauthButtonName: '好的'
      },
      nvabarData: {
        showCapsule: 1,
        // 是否显示左上角胶囊按钮 1 显示 0 不显示
        title: '登录'
      }
    };
  },

  components: {
    auth,
    navBar
  },
  props: {},
  onLoad: function () {
    var that = this;
    wx.login({
      success: function (res) {
        that.setData({
          wxCode: res.code
        });
      }
    });
  },
  onReady: function () {
    this.oauthModal = this.selectComponent("#auth");
  },
  methods: {
    phoneInput: function (e) {
      this.setData({
        phoneNumber: e.detail.value
      });
    },
    codeInput: function (e) {
      this.setData({
        codeNumber: e.detail.value
      });
    },
    //获取验证码
    codeClick: function () {
      var that = this;

      if (that.phoneNumber.length == 0) {
        wx.showToast({
          title: '请输入手机号',
          icon: 'none'
        });
      } else if (that.phoneNumber.length != 11 || that.isWrong) {
        wx.showToast({
          title: '请输入正确手机号',
          icon: 'none'
        });
      } else if (that.code == '获取验证码') {
        var keyStr = "guixue" + md5.hexMD5(that.phoneNumber); //创建参数

        var data = {
          mobile: that.phoneNumber,
          k: md5.hexMD5(keyStr)
        };
        util.getRequest("https://passport.xueweigui.com/mobile/sendOnlyMobile", data, function (res) {
          console.log(res.data);

          if (res.data.e == '9999') {
            that.codeStart();
          } else {
            wx.showToast({
              title: res.data.m,
              icon: 'none'
            });
          }
        });
      }
    },
    codeStart: function () {
      var that = this; //验证码倒计时

      that.setData({
        code: '60s'
      });
      var id = setInterval(function () {
        that.setData({
          code: parseInt(that.code) - 1 + 's'
        });

        if (parseInt(that.code) == 0) {
          that.setData({
            code: '获取验证码'
          });
          clearInterval(id);
        }
      }, 1000);
    },
    //验证码登录
    loginClick: function () {
      var that = this;

      if (that.enable) {
        if (that.phoneNumber.length == 0) {
          wx.showToast({
            title: '请输入手机号',
            icon: 'none'
          });
        } else if (that.phoneNumber.length != 11 || that.isWrong) {
          wx.showToast({
            title: '请输入正确手机号',
            icon: 'none'
          });
        } else if (that.codeNumber.length < 4) {
          wx.showToast({
            title: '验证码错误',
            icon: 'none'
          });
        } else {
          that.setData({
            enable: false
          });
          setTimeout(function () {
            that.setData({
              enable: true
            });
          }, 5000);
          var data = {
            mobile: that.phoneNumber,
            captcha: that.codeNumber
          };
          util.getRequest("https://passport.xueweigui.com/mobile/fastloginreg", data, function (res) {
            wx.showToast({
              title: '登录成功'
            });
            console.log(res);

            if (res.data.e == '9999') {
              setTimeout(function () {
                getApp().globalData.g_is_login = true;
                util.saveCookie(res.header["Set-Cookie"]);
                wx.navigateBack();
              }, 1500);
            } else {
              wx.showToast({
                title: res.data.m,
                icon: 'none'
              });
            }
          });
        }
      }
    },
    thirdClick: function () {
      this.oauthModal.oauthCheck();
    },
    //授权失败 
    oauthFailEvent: function () {
      console.log('未授权');
    },
    //绑定成功
    bindOkEvent: function () {
      console.log('绑定成功11');
      wx.showToast({
        title: '登录成功'
      });
      getApp().globalData.g_is_login = true;
      setTimeout(function () {
        wx.switchTab({
          url: '/pages/my/my'
        });
      }, 1500);
    },
    getUserInfo: function (e) {
      console.log('logig', e); // this.oauthModal.oauthCheck();

      return;
      var that = this; //发起网络请求

      if (this.wxCode) {
        var data = {
          code: this.wxCode,
          iv: e.detail.iv,
          encryptedData: e.detail.encryptedData
        };
        that.wxLogin(data);
      } else {
        wx.login({
          success: function (res) {
            var data = {
              code: res.code,
              iv: e.detail.iv,
              encryptedData: e.detail.encryptedData
            };
            that.wxLogin(data);
          }
        });
      }
    },
    wxLogin: function (data) {
      console.log(data);
      util.getRequest("https://passport.xueweigui.com/mobile/oauthlogin?type=22", data, function (res) {
        console.log(res);

        if (res.data.e == '9999') {
          util.saveCookie(res.header["Set-Cookie"]);
          wx.navigateBack();
        } else {
          wx.showToast({
            title: res.data.m,
            icon: 'none'
          });
        }
      });
    },
    // 重新授权
    againAuthorize: function (data) {
      var that = this;
      wx.openSetting({
        complete: function (res) {
          console.log(res.authSetting);

          if (res.authSetting['scope.userInfo']) {
            wx.getUserInfo({
              success: function (res) {
                console.log(res);
                data.iv = res.iv;
                data.encryptedData = res.encryptedData; //调用服务端接口

                that.wxLogin(data);
              }
            });
          }
        }
      });
    }
  }
};
